import {useState,useEffect} from 'react'
import "./News.css"
import ComponentNews1 from './NewsComponents/ComponentNews1';
import axios from '../../APi/axios-fb';


function News() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(true);
  const [items, setItems] = useState({});

  useEffect(() => {
    axios.get("/news.json")
      .then(
        (result) => {
          setItems(result.data);
          setIsLoaded(false);
        },
        
      )
      .catch((error) => {
        setIsLoaded(false);
        setError(error);
      })
  }, [])
  
  console.log(error);
  
  if (isLoaded) {
    return <div className="load_wrapepr"><h1 className="state">Loading...</h1></div>
  }

  if(error) {
    return <div className="load_wrapepr"><h1 className="state">Ошибка</h1> </div>
  }

  return (
    <div className="news_wrapper">
      <div className="news_conteiner">  
        <div className="title_wrapper">
          <h1 className="news_title">News List page</h1>
        </div>
          {
            Object.keys(items).map(key => {
              return <ComponentNews1 key={key} data={items[key]}/>
            })
          }
          
          
      </div>  
    </div>
  )
}

export default News
