import { useState } from "react";
import firebase from "../../config/fb-config";
import axios from "../../APi/axios-fb";
import "./Add.css";
function Add(props) {
  const [post, setPost] = useState({});
  const [file, setFile] = useState(null);
  const changeHandler = (e) => {
    setPost((prev) => {
      return {
        ...prev,
        [e.target.name]: e.target.value,
      };
    });
  };
  const selectHandler = (e) => {
    setFile(e.target.files[0]);
  };
  const onPostSend = (e) => {
    e.preventDefault();
    const storageRef = firebase.storage().ref("images/" + file.name);
    const uploadTask = storageRef.put(file);
    uploadTask.on(
      "state_changed",
      () => {},
      () => {},
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((fileUrl) => {
          const data = {
            ...post,
            imgUrl: fileUrl,
            imgName: file.name,
          };
          axios.post("/news.json", data).finally(() => {
            props.history.push("/news");
          });
        });
      }
    );
  };
  return (
    <div className="add_wrapper">
      <div className="add_conteiner">
          <h2 className="add_title">Add news on page</h2>
          <form id="form-add-news" onSubmit={onPostSend}>
            <input name="add_title" onChange={changeHandler} className="Add_title" type="text" placeholder="News title"/>
           
            <textarea name="text" onChange={changeHandler} className="Add_form_text" placeholder="News text"/>
            <div className="btn_wrapepr">
              <input className="add_img" type="file" id="add-img-for-new-news" onChange={selectHandler}/> 
              <input name="link_more" onChange={changeHandler} className="Add_title" type="text" placeholder="link to more info"/>
              <input name="date_send" onChange={changeHandler} className="Add_title" type="date" placeholder="date send"/>
              
              <button className="btn">Add news</button>
            </div>
          </form>
      </div>
    </div>
  );
}
export default Add;