import {useState,useEffect} from "react";
import { NavLink } from "react-router-dom";
import "./Main.css"
import axios from '../../APi/axios-fb';
import TodeyNewsCom from "./todey_news/TodeyNewsCom";
import React from "react";
import gamepad from '../../assets/img/gamepad1.png'

function Main() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(true);
  const [items, setItems] = useState({})
  const [newarrey1, setnewarrey1] = useState(null)

  useEffect(() => {
    axios.get("/news.json")
      .then(
        (result) => {
          setItems(result.data);
          setIsLoaded(false);
        },
        
      )
      .catch((error) => {
        setIsLoaded(false);
        setError(error);
      })
  }, [])
  
  
  if (isLoaded) {
    return <div className="load_wrapepr"><h1 className="state">Загрузка...</h1></div>
  }

  if(error) {
    return <div className="load_wrapepr"><h1 className="state">Ошибка</h1></div>
  }
  
  let objtomas = Object.entries(items);


  const reversed = objtomas.reverse();
  const newarrey11 = reversed[0];
  const newarrey22 = reversed[1];
  const newarrey33 = reversed[2];
  
  let newRef = React.createRef();

  function scrollto() {
    window.scroll({
      left:0,
      top: 800,
      behavior: "smooth"
    })
  }

  return <div>
    <div className="main_wrapper">

      <div className="conteiner sdas">
        <div className="titles_wrapper">
          <h1 className="title" >Новости игровой индустрии на GameVamp </h1>
          <h4 className="subtitle" >Игры, релизы и все, что касается PC, PS4, Xbox One,<br></br> Xbox 360, PS3, Wii, PSP и DS</h4>
            <div className="btn_wrapper">
            
              <a onClick={scrollto}  class="gradient-btn g" href="#!">Cегодняшние новости</a>
            
              <NavLink className="gradient-btn" to="/Signin" >Войти</NavLink>
            </div>
        </div>
        <div className="animaed-wifi">
        <svg className="svgwifi" height="100" viewBox="0 0 508 508" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M253.476 358.88C229.236 358.88 209.516 378.536 209.516 402.684C209.516 426.84 229.236 446.504 253.476 446.504C277.724 446.504 297.444 426.84 297.444 402.684C297.444 378.536 277.72 358.88 253.476 358.88Z" fill="#2b84ea"/>
      <path d="M360.732 299.197C332.112 270.593 294.016 254.833 253.476 254.833C213.092 254.833 175.1 270.489 146.488 298.913C134.932 310.413 134.912 329.149 146.44 340.669C152.04 346.277 159.496 349.357 167.432 349.357C175.332 349.357 182.76 346.293 188.352 340.733C205.764 323.429 228.892 313.897 253.476 313.897C278.152 313.897 301.344 323.489 318.768 340.897C324.368 346.489 331.82 349.577 339.748 349.577C347.656 349.577 355.08 346.513 360.676 340.953C372.248 329.452 372.268 310.725 360.732 299.197Z" fill="#2b84ea"/>
      <path d="M498.512 161.872C433.08 96.601 346.052 60.665 253.476 60.665C161.024 60.665 74.084 96.517 8.672 161.633C-2.884 173.133 -2.892 191.869 8.656 203.389C14.252 208.969 21.696 212.037 29.616 212.037C37.532 212.037 44.968 208.981 50.564 203.405C104.784 149.437 176.844 119.709 253.476 119.709C330.22 119.709 402.352 149.497 456.584 203.609C462.18 209.193 469.636 212.273 477.552 212.273C485.464 212.273 492.9 209.201 498.492 203.633C510.052 192.124 510.064 173.381 498.512 161.872Z" fill="#2b84ea"/>
      <path d="M427.876 232.256C381.3 185.788 319.364 160.2 253.476 160.2C187.708 160.2 125.864 185.708 79.328 232.012C73.724 237.584 70.64 244.996 70.628 252.888C70.628 260.772 73.7 268.192 79.296 273.772C84.892 279.356 92.34 282.436 100.26 282.436C108.168 282.436 115.604 279.364 121.208 273.804C156.56 238.632 203.536 219.256 253.476 219.256C303.52 219.256 350.56 238.7 385.936 273.992C391.54 279.584 398.984 282.664 406.908 282.664C414.82 282.664 422.26 279.592 427.848 274.024C433.444 268.452 436.536 261.04 436.54 253.148C436.548 245.256 433.468 237.844 427.876 232.256Z" fill="#2b84ea"/>
    </svg>
    <img src={gamepad} alt="png"/>
    </div>
      </div>
      

      <div className="news_card">
        <div className="news_cards_wrapper">
          
        
            
        <TodeyNewsCom ref={newRef} mass1={newarrey11} />
        <TodeyNewsCom mass1={newarrey22} />
        <TodeyNewsCom mass1={newarrey33} />
          

        </div>
        <div className="btn_wrapper">
          <NavLink className="gradient-btn mt" to="/News" >Другие новости</NavLink>
        </div>
      </div>



    </div>
  </div>;
}

export default Main;
