import React from 'react'
import './TodeyNewsCom.css'



function TodeyNewsCom(props) {
  
  console.log(props.mass1[1].add_title);
  return (
    
    
    <div className="news_Lists news_Lists_min">
          <div className="news_list_img news_list_img_min"> <img src={props.mass1[1].imgUrl} alt="news"/></div>
          <div className="news_list_content">
            <div className="news_con_title">{props.mass1[1].add_title}</div>
            <div className="news_con_text">{props.mass1[1].text}</div>
            <div className="btn_wrappers">
            <a class="gradient-btn r m0"  href={props.mass1[1].link_more}>Читать больше</a>
            </div>
          </div>
        </div>


  )
}

export default TodeyNewsCom
