import './compNews.css'
function component_news1(props) {

  
  return (
    
    
      <div className="news_Lists">
          <div className="news_list_img"> <img src={props.data.imgUrl} alt="news"/></div>
          <div className="news_list_content">
            <div className="news_con_title">{props.data.add_title}</div>
            <div className="news_con_text">{props.data.text}</div>
            <div className="btn_wrappers">
            <a class="gradient-btn r m0"  href={props.data.link_more}>Читать больше</a>
            </div>
          </div>
        </div>
    
  )
}

export default component_news1;
